export default {
	data() {
		return {
			TabCur: -1,
			scrollLeft: 0,
			swiperList: null,
			tabs: null,
			subTabs: [],
			localCity: '北京市',
			goods: null,
			cardCur: 0,
			subCurrent: 0,
			scrollTop: 0,
			coupons: [{
				money: 10,
				condition: 100
			}, {
				money: 5,
				condition: 50
			}, {
				money: 10,
				condition: 100
			}, {
				money: 2,
				condition: 10
			}, {
				money: 5,
				condition: 100
			}, {
				money: 20,
				condition: 300
			}, {
				money: 5,
				condition: 50
			}, {
				money: 2,
				condition: 10
			}],
			coupon_month: [],
			coupon_day: [],
			subTabsSelectIndex: -1,
			page: 1,
			stopLoad: false
		}
	},
	onLoad(options) {
		let _this = this
		console.log(options)
	},
	onShow() {
		//获取导航列表
		this.getTabs()
		//获取优品券列表
		// this.getCoupons()
		//获取轮播广告图
		this.getAd()
		this.stopLoad = false
		this.scrollTop = 0
		this.page = 1
		if (uni.getStorageSync('localCity')) {
			this.localCity = uni.getStorageSync('localCity')
			this.getList()
		} else {
			uni.getLocation({
				type: 'wgs84',
				success: (res) => {
					let latitude = res.latitude
					let longitude = res.longitude
					console.log('当前位置的经度：' + longitude + ',当前位置的纬度：' + latitude)
					console.log(this.mapKey)
					let mapKey = this.mapKey
					uni.request({
						url: `https://apis.map.qq.com/ws/geocoder/v1/?location=${latitude},${longitude}&key=${mapKey}`,
						success: res => {
							console.log(res.data.result.ad_info.city)
							this.localCity = res.data.result.ad_info.city
							uni.setStorageSync('localCity', this.localCity)
							this.getList()
						}
					})
				},
				fail: (err) => {
					console.log(err)
					uni.navigateTo({
						url: "../location/location"
					})
				}
			});
		}

	},
	methods: {
		//领取优品券
		receiveCoupon(id) {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/coupon/getCoupon',
				data: {
					token: uni.getStorageSync('token'),
					id: id
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						uni.showToast({
							icon: "success",
							title: "领取成功"
						})
						this.getCoupons()
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		toSearch() {
			uni.navigateTo({
				url: "../search/search"
			})
		},
		toClassifyPage(i) {
			uni.setStorageSync('classify', this.tabs[i])
			uni.navigateTo({
				url: "../classifyPage/classifyPage"
			})
		},
		//获取优品券列表
		// getCoupons() {
		// 	this.setheader()
		// 	uni.request({
		// 		url: this.websiteUrl + '/api/Coupon/indexCouponList',
		// 		data: {
		// 			token: uni.getStorageSync('token')
		// 		},
		// 		header: this.header,
		// 		success: (res) => {
		// 			console.log(res.data)
		// 			if (res.data.status == 0) {
		// 				this.coupon_month = res.data.data.coupon_month
		// 				this.coupon_day = res.data.data.coupon_day
		// 				this.coupons.push(...this.coupon_month)
		// 				this.coupons.push(...this.coupon_day)
		// 				console.log(this.coupons)
		// 				return
		// 			}
		// 			this.showToast(res.data.msg)
		// 		}
		// 	})
		// },
		getCoupons() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/index/indexCouponList',
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.coupons = res.data.data
						console.log(this.coupons)
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		myScroll(e) {
			// console.log(e.detail.scrollTop)
			this.scrollTop = 1
		},
		//跳转轮播详情页面
		toDetail(url) {
			console.log(url)
			uni.navigateTo({
				url: url
			})
		},
		
		checkMakeShop(url) {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/store/publics/selectShop',
				data: {
					user_id: uni.getStorageSync('user_id')
				},
				header: this.header,
				method: 'POST',
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data) {
							uni.navigateTo({
								url: "../beShop/steps?status=" + res.data.data.status
							})
						} else {
							uni.navigateTo({
								url: url
							})
						}
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		getPoints(url) {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/userCenter/getScore',
				data: {
					token: uni.getStorageSync('token')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.checkMakeShop(url)
						return
					}
					if (res.data.status == 1) {
						uni.navigateTo({
							url: "../wxLogin/wxLogin"
						})
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//获取广告轮播图
		getAd() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/Ad/index',
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.swiperList = res.data.data
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		toGoodsDetail(shop_id, goods_id) {
			uni.navigateTo({
				url: "../goodsDetail/goodsDetail?id=" + goods_id + "&shop_id=" + shop_id
			})
		},
		//点击一级分类标题
		tabSelect(e) {
			this.scrollTop = 0
			this.stopLoad = false
			this.TabCur = e.currentTarget.dataset.index;
			this.page = 1
			console.log(this.TabCur)
			if (this.TabCur == -1) {
				this.subTabs = []
			} else {
				this.subTabs = this.tabs[this.TabCur].category
			}
			console.log(this.subTabs)
			this.scrollLeft = (e.currentTarget.dataset.index - 1) * 60
			this.subTabsSelectIndex = -1
		},
		//点击二级分类标题
		subTabsSelect(i) {
			this.subTabsSelectIndex = i
			uni.navigateTo({
				url: '../searchDetail/searchDetail?cat_id=' + this.subTabs[i].id
			})
		},
		//跳转城市定位
		toLocation() {
			uni.navigateTo({
				url: "../location/location?city=" + this.localCity
			})
		},
		getTabs() {
			uni.request({
				url: this.websiteUrl + "/api/Goods/categoryList",
				method: "POST",
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.tabs = res.data.data
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		getCity() {
			uni.getLocation({
				type: 'wgs84',
				success: (res) => {
					let latitude = res.latitude
					let longitude = res.longitude
					console.log('当前位置的经度：' + longitude + ',当前位置的纬度：' + latitude)
					console.log(this.mapKey)
					let mapKey = this.mapKey
					uni.request({
						url: `https://apis.map.qq.com/ws/geocoder/v1/?location=${latitude},${longitude}&key=${mapKey}`,
						success: res => {
							console.log(res.data.result.ad_info.city)
							this.localCity = res.data.result.ad_info.city
							uni.setStorageSync('localCity', this.localCity)
						}
					})
				},
				fail: (err) => {
					console.log(err)
					uni.navigateTo({
						url: "../location/location"
					})
				}
			});
		},
		getList() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/Index/index',
				data: {
					city: this.localCity,
					keywords: "",
					page: 1,
					type: 0
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.goods = res.data.data
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		loadMoreGoods() {
			if (this.stopLoad) {
				return
			}
			this.page++
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/Index/index',
				data: {
					city: this.localCity,
					keywords: "",
					page: this.page,
					type: 0
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.length == 0) {
							this.stopLoad = true
							return
						}
						for (let i = 0; i < res.data.data.length; i++) {
							this.goods.push(res.data.data[i])
						}
						return
					}
					this.showToast(res.data.msg)
				}
			})
		}
	}
}
